.camera-container{
    position: relative;
    width: 100vw;
    height: 100vh;
}

.camera-container video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100% - 100px);
    object-fit: fill;
}

.camera-container img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: inherit;
}

.camera-container button {
    position: absolute;
    display: block;
    box-sizing: border-box;
}